body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{font-family:'Open Sans', sans-serif}h1{font-family:'Major Mono Display', monospace;margin:0;padding:20px}

.content{display:flex;flex-direction:column;min-height:100vh}.content header h1{margin:0;padding:20px}.content section{display:flex;justify-content:center;align-items:center;flex:1 1}.content footer{display:flex;justify-content:flex-end;padding:20px}.content footer a:visited{color:unset}.content footer a:hover{color:#404040}

